import React from 'react'
import CrudImg from '../Imagenes/CrudImg.png';
import IconoHtml from '../Componente-2/iconhtml5.png';
import IconoCss3 from '../Componente-2/iconcss3.png';
import IconoJs from '../Componente-2/iconjavascript.png';
import NodeJs from '../Componente-2/nodejs.png';
import IconoMySql from '../Componente-2/mysql3.png';
import Iconoboostrap from '../Componente-2/iconboostrap.png';




const defaultOptions = {
  loop:true,
  autoplay:true,
  rendererSetting: {
      preserveAspectRatio: 'xMidYMid slice'
  }
  

}; 



const Contenido_Proyecto_10 = () => {
  return (
    <div className="Fondo_Proyecto_1 container-fluid animate__animated animate__zoomIn">
        

    <div className="row">

      <div className="col-md-6 Columna-PrimerContenido">
      <div className="col-md-12"><h4 ><b> Crud Formulario </b></h4></div>
        <p className="col-md-12"><small>
         Crud Realizado para poner en practica el lenguaje Sql con Mysql .
         codigo Backend por medio de NodeJs y subir tanto codigo de NodeJs como base de datos a diferentes 
         servidores para tenerlo remoto.
          
          <hr></hr>
          
        </small></p>

        <div className="col-md-12">
          <p> <b>Lenguajes Utilizados</b></p>
          <img className="m-1 Icono-html-Proyectos" src={IconoHtml} />
          <img className="m-1 Icono-Js-Proyectos" src={IconoJs} />
          <img className="m-1 Icono-Js-Proyectos" src={IconoMySql} />
          <img className="m-1 Icono-NodeJs-Proyectos" src={NodeJs} />
          <img className="m-1 Icono-Js-Proyectos" src={Iconoboostrap} />
        </div>

        </div>
        <div className="col-md-6 animate__animated animate__fadeIn">
          <img className="ImagenSpoti" src={CrudImg}/>

          <div className="col-md-12">
            <a href="https://appcrudnodejs.herokuapp.com/" target="_blank" class="col-md-4 btn Botones_Estilos_2">Ver Proyecto</a>
          </div>
        </div>
         
        
        
    </div>
     


  </div>
  )
}

export default Contenido_Proyecto_10